html, body, div, span, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
abbr, address, cite, code,
del, dfn, em, img, ins, kbd, q, samp,
small, strong, sub, sup, var,
b, i,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, figcaption, figure,
footer, header, hgroup, menu, nav, section, summary,
time, mark, audio, video {
  margin:0;
  padding:0;
  border:0;
  outline:0;
  font-size:100%;
  vertical-align:baseline;
  background:transparent;
}body {
  line-height:1;
}article,aside,details,figcaption,figure,
footer,header,hgroup,menu,nav,section {
  display:block;
}nav ul {
  list-style:none;
}blockquote, q {
  quotes:none;
}blockquote:before, blockquote:after,
q:before, q:after {
  content:'';
  content:none;
}a {
  margin:0;
  padding:0;
  font-size:100%;
  vertical-align:baseline;
  background:transparent;
  text-decoration: none;
}/* 独自で追加 */img {
  width: 100%;
}.en {
  letter-spacing: 2px;
}/* change colours to suit your needs */ins {
  background-color:#ff9;
  color:#000;
  text-decoration:none;
}/* change colours to suit your needs */mark {
  background-color:#ff9;
  color:#000;
  font-style:italic;
  font-weight:bold;
}del {
  text-decoration: line-through;
}abbr[title], dfn[title] {
  border-bottom:1px dotted;
  cursor:help;
}table {
  border-collapse:collapse;
  border-spacing:0;
}/* change border colour to suit your needs */hr {
  display:block;
  height:1px;
  border:0;  
  border-top:1px solid #cccccc;
  margin:1em 0;
  padding:0;
}input, select {
  vertical-align:middle;
}*,
*::before,
*::after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}#app{font-family:'Lato', 'Noto Sans JP','\30D2\30E9\30AE\30CE\89D2\30B4   ProN', 'Hiragino Kaku Gothic ProN', '\30E1\30A4\30EA\30AA', Meiryo, '\FF2D\FF33   \FF30\30B4\30B7\30C3\30AF', 'MS PGothic', sans-serif;}.test{margin-top:200px;z-index:100}


































/* default */
.svg-icon {
  width: 1.2em;
  height: 1.2em;
  fill: currentColor;
}
/* sizes */
.svg-icon.is-large {
  width: 2em;
  height: 2em;
}
.svg-icon.is-medium {
  width: 1.6em;
  height: 1.6em;
}
.svg-icon.is-normal {
  width: 1.2em;
  height: 1.2em;
}
.svg-icon.is-small {
  width: 1em;
  height: 1em;
}
.svg-icon.is-tiny {
  width: 0.8em;
  height: 0.8em;
}
/* colors */
.svg-icon.is-primary {
  fill: #d9376e;
}

